<template>
<div>
<Header></Header>
<Slidebar @child_event="pslidefunc"></Slidebar>
 <div class="o-wrap" v-bind:class="{hide_sidebar: !slide}">
        <div class="page-wrap" id="container" data-page="/">
            <div class="page-index" _ouj_init="true"  style="margin-top:-50px;">
                <!--
                <div class="mod-location">我的位置：
                    <a data-href="" href="http://www.duowan.com/">多玩首页</a>
                    &nbsp;&gt;&nbsp;
                    <span>云玩游戏</span>
                </div>
                -->
                <div class="mod-all mt-30">               
                    <div class="g-title  g-title-filter">
                        <h2>搜索结果</h2>
                        <ul style="float:left;margin-left:10px;" v-if="keywords">
                            <li><a href="javascript:void(0);" style="width:auto;padding:2px;">{{keywords}}</a></li>
                        </ul>
                    </div>
                  <div class="no-data"  v-if="unload">
                    <p class="tips">
                        <i class="fa fa-spinner fa-pulse fa-3x fa-fw"></i>
                        <span class="sr-only">加载中...</span>
                    </p>
                  </div>
                  <div class="no-data" v-if="dataStatus">
                    <div class="img-nofound"></div>
                    <p class="tips">抱歉，没有找到您想要的内容</p>
                  </div>
                    <div class="list">
                        <ul class="g-list-1" v-bind:style="{height:curHeight}">
                            <li  v-for="site in data" style="margin-top:10px;">
                                <div class="cover-wrap">
                                        <router-link :to="'/detail'+site.id">
                                        <img :src="site.vimg" :alt="site.title">
                                        </router-link> 
                                </div>
                                <div class="ft">
                                    <div class="info">
                                        <router-link :to="'/detail'+site.id">
                                          {{site.title}}
                                        </router-link>                                        
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <ul style="margin-top:10px;" v-if="more_load">
                            <li id="cg_loader" class="more">
                                <i class="fa fa-cog fa-spin fa-3x fa-fw"></i>
                                <span class="sr-only">Loading...</span>
                            </li>
                        </ul>
                        <ul style="margin-top:10px;" v-if="more">
                            <li id="cg_loader" class="more">
                                <a class="btn-list-more" href="javascript:void(0);" @click="loadMore()">加载更多</a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
      </div>  
</div>
</template>

<script>
    import Header from './Header.vue'
    import Slidebar from './Slidebar.vue'
    export default {
        name: 'Index',
        components:{
          Header,
          Slidebar
        },
        data() {
            return {
                url: this.GLOBAL.serviceUrl+'?t=2',
                tagUrl: this.GLOBAL.serviceUrl+'?t=3',
                tableData: [],
                total:0,
                cur_page: 1,
                pageSize:0,
                pageList:[],
                tData:[],
                slide:1,
                curHeight:'height:272.767px',
                ltype:0,
                hData:[],
                more:0,
                keywords:this.$route.params.k,
                dataStatus:0,
                unload:1,
                more_load:0
            }
        },
        created() {
            this.getData();
            this.getKData()
        },
        mounted(){
            //this.getData();
        },
        watch: {
          '$route' (to,from) {
            this.cur_page = 1
            this.tableData = []
            this.more = 0
            this.getData();
            this.getKData()
            this.dataStatus = 0
            this.keywords = ''
            this.unload = 1
            this.more_load = 0
          }
        },  
        computed: {
            data() {
                return this.tableData             
            },
            curPageList() {
                return this.pageList             
            },
            tagData() {
                return this.tData
            },
            hotData() {
                return this.hData
            },
            "msg"(){
                return "asdff"
            } 
        },
        methods: {
            getType(_type=0){
              if(_type != this.ltype){
                  this.tableData = []
                  this.more = this.cur_page = 1              
              }else{
                return false
              }
              this.ltype = _type
              this.getData();
            },
            loadMore(){
              this.more_load = 1
              this.more = 0
              this.cur_page +=1
              if(this.$route.params.k !==undefined){
                this.getKData();
              }else{
                this.getData();
              }
              
            },
            pslidefunc(data){
              this.slide = parseInt(data)
              if(!data){
                this.curHeight = 'height:287.403px'
              }else{
                this.curHeight = 'height:272.767px;'
              }
              console.log(this.curHeight)
            },
            refresh(){
                this.getData();
            },
            curTitle(val){
                let obj = {};

            　　obj = this.role_list.find((item)=>{

            　　　　return item.id === val;

            　　});
                this.selected_title=obj.title
            },
            // 分页导航
            handleCurrentChange(val) {
                this.cur_page = val;
                this.getData();
            },
            getTagData() {
                var a=this.commonfunc._getRandom()
                var nonce = parseInt(a)
                var token=this.commonfunc._gtokene(nonce)
                var that = this  
                this.axios.post(this.tagUrl,this.qs.stringify({a: 1}),{
                        headers: {'Content-Type':'application/x-www-form-urlencoded','nonce':nonce,'token':token}
                    }).then(function(res){
                    if(parseInt(res.data.message)==0){                  
                       that.tData = res.data.data;   
                    }
                    //console.log(that.total)
                    //that.total=res.        
                }).catch(function(err){
                 console.log(err)
                })
                              
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                if (this.$route.params.tag=== undefined){
                    return
                }
                if(this.$route.params.tag!=''){
                      var a=this.commonfunc._getRandom()
                      var nonce = parseInt(a)
                      var token=this.commonfunc._gtokene(nonce)
                      this.tag = this.$route.params.tag 
                      var that = this  
                      this.axios.post(this.url,this.qs.stringify({page: this.cur_page,word:this.tag,stype:2}),{
                              headers: {'Content-Type':'application/x-www-form-urlencoded','nonce':nonce,'token':token}
                          }).then(function(res){
                          if(parseInt(res.data.message)==0){                  
                             that.tableData = that.tableData.concat(res.data.data); 
                             if(that.cur_page == res.data.endPage||that.cur_page > res.data.endPage){
                                that.more = 0
                             }else{
                                that.more = 1
                             } 
                             that.dataStatus = 0        
                             if(res.data.endPage<1){
                                that.dataStatus = 1
                             }   
                          }else{
                            that.dataStatus = 1
                          }
                          that.unload = 0  
                          that.more_load = 0    
                      }).catch(function(err){
                       console.log(err)
                      })
                }
                              
            },
            getKData() {
                if (this.$route.params.k=== undefined){
                    return
                }
                if(this.$route.params.k!=''){
                      var a=this.commonfunc._getRandom()
                      var nonce = parseInt(a)
                      var token=this.commonfunc._gtokene(nonce)
                      this.keywords = this.$route.params.k 
                      var that = this  
                      this.axios.post(this.url,this.qs.stringify({page: this.cur_page,word:this.keywords,stype:3}),{
                              headers: {'Content-Type':'application/x-www-form-urlencoded','nonce':nonce,'token':token}
                          }).then(function(res){
                          if(parseInt(res.data.message)==0){                  
                             that.tableData = that.tableData.concat(res.data.data); 
                             if(that.cur_page == res.data.endPage||that.cur_page > res.data.endPage){
                                that.more = 0
                             }else{
                                that.more = 1
                             } 
                             that.dataStatus = 0    
                             if(res.data.endPage<1){
                                that.dataStatus = 1
                             }
                          }else{
                            that.dataStatus = 1
                          }
                          that.unload = 0    
                          that.more_load = 0      
                      }).catch(function(err){
                       console.log(err)
                      })
                }
                              
            },

            search() {
                this.is_search = true;
                this.cur_page = 1;
                this.getData();
            },
            formatter(row, column) {
                return row.address;
            },
            filterTag(value, row) {
                return row.tag === value;
            },
            strLen(str){
                var len = 0;
                for (var i=0; i<str.length; i++) { 
                 var c = str.charCodeAt(i); 
                //单字节加1 
                 if ((c >= 0x0001 && c <= 0x007e) || (0xff60<=c && c<=0xff9f)) { 
                   len++; 
                 } 
                 else { 
                  len+=2; 
                 } 
                } 
                return len;           
            },
        }
    }

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.pActive{
  color:blue;
}
</style>
<style>

.gli_slideIn{
   height: 287.403px;
}
.gli_slideOut{
   height: 272.767px;
}
</style>



